// Body
.okinawa {
  background-color: $black;
  color: $white;
  padding: 30px;
  position: relative;
  overflow-x: hidden;

  // First section general styles
  .intro {
    height: calc(100vh - 96px - 120px); // calc(height of viewport - height of element - padding bottom and top)
  }

  .work {
    text-align: center; // for the h1
  }


  //Circle with Margarida Botelho
  .name-circle {
    width: 250px;
    heigh: 250px;

    circle {
      fill: none;
    }

    path {
      fill: $orange;
    }
  }

  // Waving hand
  .waving-hand {
    position: absolute;
    right: -20px;
    top: -20px;

    .hand-1 {
      // opacity: 0;
      // animation: hand 6s linear infinite;
    }

    .hand-2 {
      // opacity: 0;
      // animation: hand 6s linear infinite;
      // animation-delay: -3s;
    }

    .hand-3 {
      // opacity: 0;
      // animation: hand 6s linear infinite;
      // animation-delay: -6s;
    }
  }

  // Work experience box
  .work-experience { //h1
    display: inline-block;
    background-color: $yellow;
    padding: 30px 100px;
    color: $black;
    position: relative;
    margin: 0;
    margin-bottom: 100px;

    &:after,
    &:before {
      content: "";
      border-radius: 100px;
      width: 96px;
      height: 96px;
      background-color: $yellow;
      position: absolute;
      top: 0;
      background-image: url('images/point-down.svg');
      background-repeat: no-repeat;
      background-position: center;
    }

    &:before {
      left: -130px;
    }

    &:after {
      right: -130px;
    }
  }
}
